apshotRow {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    gap: 20px;\n\n    flex-wrap: wrap;\n\n    width: 100%;\n}\n\n.snapshotLabel {\n    font-size: 25px;\n}\n\n.weatherValue {\n    font-size: 25px;\n}\n\n\n\n.hidden {\n    display: none;\n}\n\n.footer {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    font-size: 15px;\n    gap: 5px;\n\n}\n\n#githubLogo {\n    height: 20px;\n    border-radius: 10px;\n\n}\n\n.weather-table {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.weather-row {\n    display: flex;\n    gap: 20px;\n    border: 1px solid black;\n    height: minmax(fit-content, 100px);\n    align-items: center;\n    justify-content: center;\n    padding: 5px;\n    width: 85%;\n    flex-wrap: wrap;\n    \n    \n}\n.weather-value {\n    margin-left: auto;\n    margin-right: 0;\n\n    font-family: \"Absans-Regular\";\n    font-weight: 600;\n    font-size: 17px;\n}\n\n.hourlyDiv {\n    margin-top: 20px;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    margin-top: 20px;\n    \n}\n\n.weatherHourRow {\n    display: flex;\n    gap: 20px;\n    border: 1px solid black;\n    align-items: center;\n    justify-content: space-between;\n    padding: 5px;\n    width: 85%;\n    height: 150px;\n    text-align: left;\n    flex-wrap: wrap;\n\n    font-family: \"Absans-Regular\";\n    font-weight: 600;\n    font-size: 17px;\n}\n\n.weatherRowCellTitle{\n    width: 75px;\n    text-align: left;\n}\n\n.weatherRowTitle {\n    display: flex;\n    gap: 20px;\n    border: 1px solid black;\n    height: fit-content;\n    align-items: center;\n    justify-content: space-between;\n    padding: 5px;\n    width: 85%;\n    flex-wrap: wrap;\n\n    font-family: \"Absans-Regular\";\n    font-weight: 600;\n    font-size: 17px;\n}\n\n.weatherHourRow img {\n    width: 75px\n}\n\n.icon {\n    width: 100px;\n    flex-shrink: 0;\n    flex-grow: 0;\n}\n\n.weekForecastDiv {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    margin-top: 20px;\n    margin-bottom: 20px;\n}\n\n.weather-label {\n    font-family: \"Absans-Regular\";\n    font-weight: 600;\n    font-size: 17px;\n}\n\n.weatherCellData {\n    width: 75px;\n    flex-grow: 1;\n    text-align: left;\n}\n\n.weatherCellData.icon {\n    flex-grow: 0;\n}\n\nbutton.resetButton {\n    border-radius: 10px;\n    background-color: transparent;\n    border-width: 3px;\n    border-color: rgb(115, 61, 61);\n\n    font-family: \"Absans-Regular\";\n    font-size: 25px;\n    font-weight: 800;\n}\n\nbutton.locationButton {\n    border-radius: 10px;\n    background-color: transparent;\n    border-width: 3px;\n    border-color: rgb(115, 61, 61);\n\n    font-family: \"Absans-Regular\";\n    font-size: 15px;\n    font-weight: 800;\n}\n\ninput#locationSearch {\n    border-radius: 10px;\n    border-width: 3px;\n\n    font-family: \"Absans-Regular\";\n    font-size: 15px;\n    font-weight: 800;\n}"],"sourceRoot":""}]);
